body {
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 13px;
}

.description {
  font-size: 12px;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60px 1fr 60px;
  grid-template-areas: "header" "body" "footer";
  height: 100vh;
  width: 100vw;
}

.chat-body {
  grid-area: body;
  overflow: auto;

  .message-container {
    margin-bottom: 8px;

    .message-wrapper {
      max-width: 60%;
      display: inline-block;

      .content {
        padding: 8px;
        border-radius: 8px;
        background-color: #e0e0e0;
      }
    }
  }
}

.header {
  grid-area: header;
  border-bottom: 1px solid #e0e0e0;
  padding: 10px;
  display: flex;
  justify-content: space-between;

  .bot-intro {
    display: flex;
    align-items: center;

    .bot-logo {
      margin-right: 10px;
      flex-grow: 0;

      img {
        height: 40px;
        width: 40px;
        border-radius: 50%;
      }
    }

    .bot-details {
      flex-grow: 1;
    }
  }
}

.footer {
  grid-area: footer;
  border-top: 1px solid #e0e0e0;
  display: flex;
  align-items: center;
  padding: 0 16px;

  #chat-input {
    flex-grow: 1;
    outline: none;
    border: none;
    color: #696969;
  }

  .icon {
    font-size: 14px;
    border-radius: 50%;
    height: 32px;
    width: 32px;
    display: flex;
    background: #2b4f70;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    .fa {
      transform: rotate(45deg);
      transition-duration: .3s;
      cursor: pointer;
      color: #fff;
    }

    &:hover {
      .fa {
        transform: rotate(0);
      }
    }

  }
}
